@import '../../styles/main.module.scss';

.wrapper {
	display: flex;
	flex-direction:row;
	justify-content: space-between;
	background-color: #f3f6fb;
	box-sizing: border-box;
	padding: 15px 40px 15px 0;
	height: 50px;
	a{
		color: inherit;
	}
	@media only screen and (max-width: 705px){
		padding: 10px 10px 15px 10px;
		flex-direction:column;
		align-items: center;
		height: auto;
  }
};

.wrapper2 {
	display: flex;
	flex-direction:row;
	justify-content: space-between;
	background-color: #f3f6fb;
	box-sizing: border-box;
	padding: 15px 40px 15px 0;
	height: 50px;
	a{
		color: inherit;
	}
	@media only screen and (max-width: 705px){
		padding: 10px 10px 15px 10px;
		flex-direction:column;
		align-items: center;
		height: 400px;
  }
};

.linksBlock {
	display: flex;
	@media only screen and (max-width: 705px){
		flex-direction:column;
		align-items: center;
  }
}

.link {
	margin-left: 40px;
	padding-top: 10px;
	padding-bottom: 22px;
	vertical-align:middle;
	display: flex;
	align-items: center;
	flex-wrap: nowrap;

	border-bottom: 3px solid #f3f6fb;
	@media only screen and (max-width: 705px){
		margin: 0 10px 0 0;
		padding: 0;
  }
}

.selected {
	border-bottom: 3px solid #488dd1;
}
